import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { IcdService } from 'src/shared/services/icd.service';
import { IcfService } from 'src/shared/services/icf.service';

@Component({
    selector: 'app-instruction-icf',
    templateUrl: './instruction-icf.component.html',
    styleUrls: ['./instruction-icf.component.scss'],
})
export class InstructionIcfComponent implements OnInit {
    documents: any[] = [];
    selectedDocument: any = null;
    selectedItemId: number | null = null;

    constructor(public icdService: IcdService,public icfService:IcfService,private toastService:ToastrService) {}

    ngOnInit(): void {
        this.loadDocumentList('ICF');
    }

    loadDocumentList(category: string) {
        this.icfService.getDocumentList(category).subscribe((data: any) => {
            this.documents = data.data;
        });
    }

    showDocumentDetails(id: number) {
        this.icdService.getDocumentDetails(id).subscribe(
            (data: any) => {
                // On successful response
                this.selectedDocument = data.data;
                this.selectedItemId = id;
            },
            (error: any) => {
                // On error
                console.error('An error occurred while fetching document details:', error);
                // Handle error here, for example, show a message to the user
                this.toastService.error(`${error?.error.error}`, 'Thất bại');

            }
        );
    }
    
}
