import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { IcdService } from 'src/shared/services/icd.service';

@Component({
    selector: 'app-instruction-icd9',
    templateUrl: './instruction-icd9.component.html',
    styleUrls: ['./instruction-icd9.component.scss'],
})
export class InstructionIcd9Component implements OnInit {
    documents: any[] = [];
    selectedDocument: any = null;
    selectedItemId: number | null = null;

    constructor(public icdService: IcdService,private toastService:ToastrService) {}

    ngOnInit(): void {
        this.loadDocumentList('ICD9');
    }

    loadDocumentList(category: string) {
        this.icdService.getDocumentList(category).subscribe((data) => {
            this.documents = data.data;
            this.showDocumentDetails(this.documents[0].id);
        });
    }

    showDocumentDetails(id: number) {
        this.icdService.getDocumentDetails(id).subscribe(
            (data: any) => {
                // On successful response
                this.selectedDocument = data.data;
                this.selectedItemId = id;
            },
            (error: any) => {
                // On error
                console.error('An error occurred:', error);
                this.toastService.error(`${error?.error.error}`, 'Thất bại');

            }
        );
        
    }
}
