import { Component, OnInit } from '@angular/core';
// import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { of } from 'rxjs';
 export const API_BASE = "https://icd.whiteneuron.com";
 export const API_TOOL = `${API_BASE}/api/ucod/diagnose`; // Consistent API URL usage

@Component({
  selector: 'app-tools-ucodvn',
  templateUrl: './tools-ucod.component.html',
  styleUrls: ['./tools-ucod.component.scss']
})
export class ToolsUcodComponent implements OnInit {
  isActive: boolean = true;

  
  toggleActive(): void {
    this.isActive = !this.isActive;
  }

  isModalVisible = false;
  currentLanguage: 'vi' | 'en' = 'vi';

  
  content = {
    vi: {
      title: 'Tiêu đề',
      short: 'Mô tả ngắn',
      version: 'Phiên bản',
      change_lang: 'Thay đổi ngôn ngữ',
      mccd: 'MCCD',
      sex: 'Giới tính',
      age: '9 năm',
      age_unit1: 'Năm',
      partI: 'Phần I',
      partI_desc: 'Mô tả phần I',
      partII: 'Phần II',
      partII_desc: 'Mô tả phần II',
      warning: 'Cảnh báo',
      warning_desc: 'Mô tả cảnh báo',
      btn_run: 'Chạy',
      conclusion: '',
      explanation: ''
    },
    en: {
      title: 'Title',
      short: 'Short Description',
      version: 'Version',
      change_lang: 'Change Language',
      mccd: 'MCCD',
      sex: 'Sex',
      age: '9 years',
      age_unit1: 'Years',
      partI: 'Part I',
      partI_desc: 'Description of Part I',
      partII: 'Part II',
      partII_desc: 'Description of Part II',
      warning: 'Warning',
      warning_desc: 'Description of Warning',
      btn_run: 'Run',
      conclusion: '',
      explanation: ''
    }
  };
  tablesection :any;
  tablerela :any;
  table: any ;
  result : any;
  sex: string = ''; // 'm' for male, 'f' for female
  age: string = '';   // Default age
  ageUnit: string = 'y'; // Default age unit
  partI: string = '';
  partII: string = '';
  conclusion: string = this.content.vi.conclusion;
  explanation: string = this.content.vi.explanation;
  
  // Diagnosis data array
  diagnosisData: { code: string; name: string; part: string; line: number; note: string }[] = [];

  constructor(private http: HttpClient) {}

  ngOnInit() {}

  
  updateDiagnosisData() {
    this.diagnosisData = []; 
    this.processCodes(this.partI.split('\n'), 'I');
    this.processCodes(this.partII.split(','), 'II');
  }

  
  private processCodes(codesArray: string[], part: string) {
    codesArray.forEach((code, index) => {
      const trimmedCode = code.trim();
      if (trimmedCode) { 
        this.diagnosisData.push({
          code: trimmedCode,
          name: '', 
          part,
          line: index + 1,
          note: ''
        });
      }
    });
  }

 
  onCodesIChange() {
    this.updateDiagnosisData();
    this.onInputchange();
  }

  onCodesIIChange() {
    this.updateDiagnosisData();
    this.onInputchange();
  }

  openModal() {
    this.isModalVisible = true;
  }

  closeModal() {
    this.isModalVisible = false;
  }

  toggleLanguage() {
    this.currentLanguage = this.currentLanguage === 'vi' ? 'en' : 'vi';
    this.updateLanguageContent();
    this.onInputchange();
  }

  updateLanguageContent() {
    this.conclusion = this.content[this.currentLanguage].conclusion;
    this.explanation = this.content[this.currentLanguage].explanation;
  }


  onSubmit() {
      const formData = new FormData(); 
      // Set default values if inputs are not provided
    let sex = (this.sex === 'Nam' ? 'm' : 'f') ; // Default: 'không xác định' for undefined sex
    let age = this.age || '9'; // Default age is 9 if not provided
    // let ageUnit = this.ageUnit || 'y'; // Default age unit is 'y' (years) if not provided
   let ageUnit = this.ageUnit ==='Năm'?'y':'m'
   
    // formData.append('csrfmiddlewaretoken', 'bgCPj1KjnvTj0oLo9rvvvv...'); // Thay token thật
    formData.append('partI', this.partI.trim());
    formData.append('partII', this.partII.trim()); // Giữ trống nếu không có giá trị
    formData.append('age',age  );
    formData.append('ageUnit',ageUnit); // "y" là years
    formData.append('sex', sex); // "m" là giới tính nam
    formData.append('lang', 'VI'); // "VI" là tiếng Việt
    
      const validation = this.isValidInput(formData);
      if (!validation.isValid) {
        // Handle invalid input
        alert(validation.messages.join('\n'));
        return; // Exit the method if validation fails
      }
      
    
    // Gửi yêu cầu POST
    this.http.post('https://icd.whiteneuron.com/api/ucod/diagnose', formData).subscribe(
      
        
      (response: any) => {
        
        if (response){
          this.conclusion = response?.result
          this.explanation = response?.table
           this.table= response.log
           this.tablerela=response.matrix
           this.tablesection= response.desc
          
        }
        
      },
      (error) => {
        console.error('Error:', error);
       this.handleApiError(error);
      }
    );
  }

  onInputchange()
  { 

   
   
    const formData = new FormData();
    
    let sex = this.sex == 'Nam'? 'm':'f'
    let ageUnit = this.ageUnit ==='Năm'?'y':'m'
    // formData.append('csrfmiddlewaretoken', '');   
    formData.append('text1', this.partI.trim());
    formData.append('text2', this.partII.trim()); // Giữ trống nếu không có giá trị
    formData.append('age', this.age +'' );
    formData.append('age_unit',ageUnit); // "y" là years
    formData.append('sex', sex); // "m" là giới tính nam
    formData.append('lang', 'VI');
 
    
// Get the CSRF token
// formData.append('csrfmiddlewaretoken', csrfToken);
// Set headers with the CSRF token
// const headers = new HttpHeaders({
//   'X-CSRFToken': csrfToken ,
  
// });

// Make the POST request with CSRF token and form data
this.http.post(`https://icd.whiteneuron.com/api/ucod/liveucod?lang=${this.currentLanguage}`, formData).subscribe(
   (response: any) => {
        
    if (response){
      this.conclusion = response?.result
      this.explanation = response?.table
       this.table= response.log
       this.tablerela=response.matrix
       this.tablesection= response.desc
      
    }
    
  },
  (error:any) => console.error(error)
);
  
 }


  public isValidInput(data: FormData): { isValid: boolean; messages: string[] } {
    const messages: string[] = [];
    
    // Validate sex
    // if (data.get('sex') !== 'm' && data.get('sex') !== 'f') {
    //     messages.push(this.currentLanguage === 'vi' ? 'Giới tính không hợp lệ.' : 'Invalid sex.');
    // }

    // Validate age
    // const age = parseInt(data.get('age') as string, 10);
    // if (isNaN(age) || age <= 0) {
    //     messages.push(this.currentLanguage === 'vi' ? 'Tuổi phải là một số dương.' : 'Age must be a positive number.');
    // }

    // Validate partI
    const partI = data.get('partI') as string;
    if (!partI || !partI.trim()) {
        messages.push(this.currentLanguage === 'vi' ? 'Phần I không được để trống.' : 'Part I cannot be empty.');
    }

    // Validate partII
    // const partII = data.get('partII') as string;
    // if (!partII || !partII.trim()) {
    //     messages.push(this.currentLanguage === 'vi' ? 'Phần II không được để trống.' : 'Part II cannot be empty.');
    // }

    return { isValid: messages.length === 0, messages };
  }


  
  private callDiagnoseApi(data: any): Observable<any> {
    const body = new HttpParams()
      .set('partI', data.partI)
      .set('partII', data.partII)
      .set('age', data.age.toString()) 
      .set('ageUnit', data.ageUnit)
      .set('sex', data.sex)
      .set('lang', this.currentLanguage); 
    const headers = new HttpHeaders({
      'Content-Type': 'application/json',

    });
    let res =  this.http.post<any>(API_TOOL, body.toString(), { headers });

    return res;
  }

  
  private handleApiError(error: any) {
    let errorMessage = this.currentLanguage === 'vi' 
      ? 'Có lỗi xảy ra trong quá trình kết nối.' 
      : 'A connection error occurred.';
  
    
    if (error?.error?.message) {
      errorMessage = error.error.message;
    } else if (error?.message) {
      errorMessage = error.message;
    }
  
    
    alert(errorMessage);
  }
}