    tNum(number: number): string {
        if (number > 1000) {
            return (number / 1000).toFixed(0).replace(/\B(?=(\d{3})+(?!\d))/g, ',') + ',000';
        } else {
            return number.toString();
        }
    }
    reSync() {
        if (this.startDate && this.endDate) {
            const param = `?&from=${format(this.startDate, 'yyyy-MM-dd')}&to=${format(
                this.endDate,
                'yyyy-MM-dd'
            )}&view=${this.viewType}&city=${
                this.prov_name_s.value?.map((item) => this.getCodeUnknow(item)).join(',') || ''
            }&csyt=${this.list_hos_s.value?.join(',') || ''}&mabenh=${
                this.list_dis_s.value?.join(',') || ''
            }`;
            this.syncChart(param);
        } else {
            const param = `?&from=${format(
                new Date().setDate(new Date().getDate() - 28),
                'yyyy-MM-dd'
            )}&to=${format(new Date(), 'yyyy-MM-dd')}&view=${this.viewType}&city=${
                this.prov_name_s.value?.map((item) => this.getCodeUnknow(item)).join(',') || ''
            }&csyt=${this.list_hos_s.value?.join(',') || ''}&mabenh=${
                this.list_dis_s.value?.join(',') || ''
            }`;
            this.syncChart(param);
        }
    }

    syncChart(param?: string) {
        const container = document.getElementById('boxChart__container');
        if (container) {
            container.addEventListener('mousemove', this.handleChartEvents);
            container.addEventListener('touchmove', this.handleChartEvents);
            container.addEventListener('touchstart', this.handleChartEvents);
        }
        this.dashboardService.sync(param || '?from=&to=').subscribe(
            (results: any) => {
                const dom_chart = document.getElementById('boxChart__container');
                if (dom_chart) {
                    const html = results.datasets
                        .map(
                            (_: any, i: any) => `
                <div id="chartSync${i}"></div>
                `
                        )
                        .join('');
                    dom_chart.innerHTML = `<div style="grid-template-columns: repeat(${Math.round(
                        results.datasets.length / 2
                    )}, minmax(0, 1fr));" class=" grid">${html}</div>`;
                }
                results.datasets.forEach((dataset: any, i: any) => {
                    const data = dataset.data.map((val: any, j: any) => [results.xData[j], val]);
                    this.Highcharts?.chart(`chartSync${i}`, {
                        chart: {
                            marginLeft: 40, // Keep all charts left aligned
                            spacingTop: 20,
                            spacingBottom: 20,
                            height: 250,
                            zooming: {
                                type: 'x',
                            },
                        },
                        exporting: {
                            enabled: false,
                        },
                        title: {
                            text: dataset.name,
                            align: 'left',
                            margin: 0,
                            x: 30,
                        },
                        credits: {
                            enabled: false,
                        },
                        legend: {
                            enabled: false,
                        },
                        xAxis: {
                            crosshair: true,
                            events: {
                                setExtremes: this.syncExtremes,
                            },
                            type: 'datetime',
                        },
                        yAxis: {
                            title: {
                                text: null,
                            },
                            crosshair: true,
                        },
                        tooltip: {
                            positioner: function (this: any) {
                                return {
                                    // right aligned
                                    x: this.chart.chartWidth - this.label.width,
                                    y: 10, // align to title
                                };
                            },
                            borderWidth: 0,
                            backgroundColor: 'none',
                            pointFormat: '{point.y}',
                            headerFormat: '',
                            shadow: false,
                            style: {
                                fontSize: '18px',
                            },
                            valueDecimals: 0,
                        },
                        plotOptions: {
                            series: {
                                marker: {
                                    enabledThreshold: 4,
                                },
                            },
                        },
                        series: [
                            {
                                data: data,
                                name: dataset.name,
                                type: dataset.type,
                                color: (this.Highcharts.getOptions() as any).colors[i],
                                fillOpacity: 0.3,
                                tooltip: {
                                    valueSuffix: ' ' + dataset.unit,
                                },
                            },
                        ],
                    } as any);
                });
            },
            (err) => {
                this.toastService.error(`Có lỗi xảy ra`, 'Thất bại');
            }
        );
    }

    handleChartEvents(e: any) {
        let chart: Highcharts.Chart;
        let point: Highcharts.Point | undefined;
        let event: Highcharts.PointerEventObject;
        let loop_length = 0;
        if (this.Highcharts) {
            loop_length = this.Highcharts.charts.length;
        }
        for (let i = 0; i < loop_length; i++) {
            chart = this.Highcharts?.charts[i] as Highcharts.Chart;
            if (!chart) continue;
            event = chart.pointer.normalize(e); // Find coordinates within the chart
            // Adjust event.chartX if necessary (custom logic)
            event.chartX = e.offsetX;

            point = chart.series[0].searchPoint(event, true); // Get the hovered point
            if (point) {
                (point as any).highlight(e);
            }
        }
    }
