    getCode() {
        const city = this.stateForm.get('vn_prov_d');
        if (city !== null && (this.startDate !== null || this.endDate !== null)) {
            // Kiểm tra city không null và startDate hoặc endDate không null
            const cityName: string | null = city.value;

            if (cityName !== null && vn_code.hasOwnProperty(cityName)) {
                // Kiểm tra xem cityName không null và có trong danh sách mã code không
                return vn_code[cityName];
            }
        }
    }
    getCodeUnknow(name: string) {
        const city = name;
        if (city !== null && (this.startDate !== null || this.endDate !== null)) {
            // Kiểm tra city không null và startDate hoặc endDate không null
            const cityName: string | null = city;

            if (cityName !== null && vn_code.hasOwnProperty(cityName)) {
                // Kiểm tra xem cityName không null và có trong danh sách mã code không
                return vn_code[cityName];
            }
        }
    }
