    formatDateLine(date?: Date, type?: string) {
        if (type) {
            if (this.dateType === 'day') {
                return format(date || new Date().setDate(new Date().getDate() - 28), 'dd/MM/yyyy', {
                    locale: vi,
                });
            }
            if (this.dateType === 'month') {
                return (
                    'T' +
                    format(date || new Date().setDate(new Date().getDate() - 28), 'M, yyyy', {
                        locale: vi,
                    })
                );
            }
            if (this.dateType === 'year') {
                return format(date || new Date().setDate(new Date().getDate() - 28), 'yyyy', {
                    locale: vi,
                });
            }
        } else {
            if (this.dateType === 'day') {
                return format(date || new Date(), 'dd/MM/yyyy', {
                    locale: vi,
                });
            }
            if (this.dateType === 'month') {
                return (
                    'T' +
                    format(date || new Date(), 'M, yyyy', {
                        locale: vi,
                    })
                );
            }
            if (this.dateType === 'year') {
                return format(date || new Date(), 'yyyy', {
                    locale: vi,
                });
            }
        }
        return '';
    }
    formatDate2(date?: Date, type?: string) {
        if (type) {
            return format(date || new Date().setDate(new Date().getDate() - 28), 'dd/MM/yyyy', {
                locale: vi,
            });
        } else {
            return format(date || new Date(), 'dd/MM/yyyy', { locale: vi });
        }
    }

    formatDate(date?: Date, type?: string) {
        if (type) {
            return format(
                date || new Date().setDate(new Date().getDate() - 28),
                "dd 'tháng' M, yyyy",
                { locale: vi }
            );
        } else {
            return format(date || new Date(), "dd 'tháng' M, yyyy", { locale: vi });
        }
    }
