    private _filterGroup(value: string): any[] {
        if (value) {
            return this.vn_prov_d
                .map((group: any) => ({ letter: group.letter, names: _filter(group.names, value) }))
                .filter((group: any) => group.names.length > 0);
        }

        return this.vn_prov_d;
    }
