import { NestedTreeControl } from '@angular/cdk/tree';
import { ViewportScroller } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';
import { MatTreeNestedDataSource } from '@angular/material/tree';
import { ActivatedRoute, Router } from '@angular/router';
import { BASE } from 'src/shared/services/icd.service';
import { SearchService } from 'src/shared/services/search.search';
import { EventManager } from '../../../../shared/services/broadcast.service';
import { IcfService } from './../../../../shared/services/icf.service';
import { ToastrService } from 'ngx-toastr';

interface Chapter {
    model: string;
    Code?: string;
    ID: string;
    Name: string;
    children?: Chapter[];
    is_leaf: boolean;
    data?: Chapter[];
    expanded?: boolean;
}

@Component({
    selector: 'app-icf',
    templateUrl: './icf.component.html',
    styleUrls: ['./icf.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class IcfComponent implements OnInit {
    chapter: any[] = [];
    infoModel: any;
    htmlRoot:any;
    dataSource = new MatTreeNestedDataSource<Chapter>();
    treeControl = new NestedTreeControl<Chapter>((node) => node.children);
    loaded: Set<string> = new Set<string>();
    isActive: string | null = '';
    language: any;
    userCommentList: any[] = [];
    showingComments = false;
    userCaptcha: string = '';
    errorMessage: string | undefined;
    contentMessage: string | undefined;
    nameMessage: string | undefined;
    emailMessage: string | undefined;
    phoneMessage: string | undefined;
    subjectMessage: string | undefined;
    isLeaf: boolean;
    root: string;
    public captchaImageUrl: string | undefined;
    isSearching: boolean = false;

    constructor(
        private viewportScroller: ViewportScroller,
        private router: Router,
        private scroller: ViewportScroller,
        private route: ActivatedRoute,
        private eventManager: EventManager,
        private _changeDetectorRef: ChangeDetectorRef,
        private searchService: SearchService,
        private ICFService: IcfService,
        private toastService: ToastrService
    ) {
        this.dataSource.data = [];
        this.eventManager.subscribe('changeLanguage', (lang: any) => {
            this.language = lang.content;
            this.getChapter();
        });
    }

    hasChild = (_: number, node: Chapter) => !node.is_leaf;

    ngOnInit(): void {
        const langue = localStorage.getItem('lang');
        this.language = langue === 'en' ? langue : 'vn';
        this.getRoot();
        this.getChapter();
        this.isActive = this.route.snapshot.queryParamMap.get('ID');
        this.getListFeedback();
    }

    getRoot() {
        this.ICFService.getRoot(this.language).subscribe((res: any) => {
            this.root = res.data[0].Name;
        });
    }

    getChapter() {
        this.ICFService.getChapter(this.language).subscribe((res: any) => {
            this.chapter = res.data;
            this.dataSource.data = this.chapter;
            this.infoModel = res.html;
            this.treeControl.dataNodes = this.chapter;
        });
    }

    getListFeedback() {
        this.ICFService.getListFeedback().subscribe((res: any) => {
            this.userCommentList = res.data;
        });
    }

    toggleForm() {
        this.showingComments = !this.showingComments;
    }

    private getFeedbackData() {
        const name = (document.getElementById('name') as HTMLInputElement).value;
        const number = (document.getElementById('number') as HTMLInputElement).value;
        const email = (document.getElementById('email') as HTMLInputElement).value;
        const title = (document.getElementById('title') as HTMLInputElement).value;
        const feedback = (document.getElementById('feedback') as HTMLTextAreaElement).value;

        return {
            app: 'ICF',
            name: name,
            phone: number,
            email: email,
            subject: title,
            content: feedback,
            captcha: this.userCaptcha,
        };
    }

    private updateCaptchaImage(newCaptchaImageUrl: string) {
        const timestamp = new Date().getTime();
        this.captchaImageUrl = newCaptchaImageUrl + `?timestamp=${timestamp}`;
    }

    private handleSuccess(res: any) {
        this.captchaImageUrl = undefined;
        this.getListFeedback();
        if (res.message) {
            this.showingComments = true;
        }
    }

    private handleError(error: any) {
        this.errorMessage = error.error?.message;
        // this.contentMessage = error.error.data?.content;
        this.nameMessage = error.error.data?.name;
        this.emailMessage = error.error.data?.email;
        this.phoneMessage = error.error.data?.phone;
        this.subjectMessage = error.error.data?.subject;

        if (error.error.data?.captcha_image) {
            const newCaptchaImageUrl = BASE + error.error.data.captcha_image;
            this.updateCaptchaImage(newCaptchaImageUrl);
        }

        this.userCaptcha = '';
    }

    submitForm() {
        const feedbackData = this.getFeedbackData();

        this.ICFService.submitFeedback(feedbackData).subscribe(
            (res: any) => {
                if (res.status === 'success')
                    this.toastService.success(`${res?.message}`, 'Thành công');
                else this.toastService.error(`${res?.message}`, 'Thất bại');
                if (res.captcha_image && this.captchaImageUrl !== res.captcha_image) {
                    const newCaptchaImageUrl = BASE + res.captcha_image;
                    this.updateCaptchaImage(newCaptchaImageUrl);
                    this.userCaptcha = '';
                } else {
                    this.handleSuccess(res);
                }
            },
            (error: any) => {
                this.handleError(error);
            }
        );
    }

    getDetailModel(item: any) {
        this.isSearching = true;
        this.htmlRoot=""
        this.ICFService.getDetailOfModel(this.language, item.ID, item.model).subscribe(
            (res: any) => {
                this.isSearching = false;
                this.infoModel = res.data.data;
            }, (err) => {
                this.isSearching = false;
            this.toastService.error(`${err?.error.error}`, 'Thất bại');

            }
        );
    }

    convertChapter(input: any): Chapter {
        return {
            ID: input.data.ID,
            model: input.model,
            Code: input.data.Code,
            Name: input.data.Name,
            is_leaf: input.is_leaf,
            children: input.children?.map((child: any) => this.convertChapter(child)),
        };
    }

    getChildrenOfModel(item: Chapter) {
        if (this.loaded.has(item.ID)) {
            return;
        }
        this.loaded.add(item.ID);
        this.ICFService.getChildrendModel(this.language, item?.ID, item?.model).subscribe(
            (res: any) => {
                // const children = res.data.map((value: any) => this.convertChapter(value));
                item.children = res.data;
                this.dataSource.data = [];
                this.dataSource.data = this.chapter;
                setTimeout(() => {
                    this.treeControl.expand(item);
                });
            }
        );
    }

    getChildrenOfTree(item: Chapter) {
        this.getChildrenOfModel(item);
        this.refreshTree();
    }

    goToDetail(item: Chapter) {
        this.isActive = item.ID;
        this.getDetailModel(item);
    }

    receiveMessage($event: any) {
        this.infoModel = $event;
        this.isActive = $event.ID;
        this.getListTreeOfSearch($event);
        this.handleScrollTree($event.ID || $event.data.ID);
    }

    openItem(node: Chapter): void {
        if (node.children) this.treeControl.expand(node);
        node.children?.forEach((item, _) => {
            this.openItem(item);
        });
    }

    refreshTree() {
        let _data = this.dataSource.data;
        this.dataSource.data = [];
        this.dataSource.data = _data;
    }

    getListTreeOfSearch(item: any) {
        this.ICFService.getChildrenOfModel(this.language, item.ID, item.model).subscribe(
            (res: any) => {
                const find = res.children.find((el: any) => el.children?.length > 0);
                const findnode = this.treeControl.dataNodes.find((dtn) => {
                    return dtn.ID === find.ID;
                });
                const children: Chapter[] = find.children.map((child: any) =>
                    this.convertChapter(child)
                );
                if (findnode) {
                    findnode.children = children;
                    this.dataSource.data = [];
                    this.dataSource.data = this.chapter;
                    this.openItem(findnode);
                }
            }
        );
    }

    goToOpenTree(event: any) {
        event.preventDefault();
        let item = {
            model: '',
            ID: '',
        };
        let href = '';
        let target = event.target || event.srcElement;
        while (target) {
            if (target instanceof HTMLAnchorElement) {
                href = target.getAttribute('href')!;
                break;
            }
            target = target.parentNode;
        }
        item.ID = href.split('/')[1];
        item.model = href.split('/')[0];
        if (item && item.ID && item.model) {
            this.isActive = item.ID;
            this.getListTreeOfSearch(item);
            this.getDetailModel(item);
            this.handleScrollTree(item.ID);
        }
    }

    handleScrollTree(id: any) {
        setTimeout(() => {
            this.router.navigate([], { fragment: id }).then((res: any) => {
                const element = document.getElementById(id);
                if (element != undefined) element.scrollIntoView({ behavior: 'smooth' });
            });
        }, 1000);
    }
}
